.inputfile
  position: absolute
  z-index: -1
  overflow: hidden
  width: 0.1px
  height: 0.1px
  opacity: 0
  + label
    display: inline-block
    overflow: hidden
    padding: 1em 1.5em
    max-width: 80%
    border-radius: 4px
    background-color: $color
    color: #ffffff
    text-overflow: ellipsis
    white-space: nowrap
    font-weight: 700
    cursor: pointer
    i
      margin-right: 1em
    span
      font-size: 1.25em
  &:focus, &.has-focus, &:hover
    + label
      background-color: darken($color, 5%)
