.timeline
  position: relative
  color: #999999

.timeline h2
  margin-top: 15px
  margin-bottom: 30px
  color: #333333
  text-align: center
  font-weight: 900
  font-size: 20px

.timeline .action
  position: relative
  margin-top: 20px
  margin-left: 35px
  background: #ffffff

.timeline .action .action-inner
  position: relative
  margin-bottom: 100px
  padding: 8px
  border: 1px solid rgba(240, 240, 240, 1)
  border-radius: 4px

.timeline .action .action-inner:before, .timeline .action .action-inner:after
  position: absolute
  top: 13px
  right: 100%
  width: 0
  height: 0
  border: solid transparent
  content: " "
  pointer-events: none

.timeline .action .action-inner:after
  margin-top: -5px
  border-width: 5px
  border-color: transparent
  border-right-color: #ffffff

.timeline .action .action-inner:before
  margin-top: -6px
  border-width: 6px
  border-color: transparent
  border-right-color: #dfdfdf

.timeline .action .action-header
  margin-bottom: 5px

.timeline .action .action-header h3
  margin: 0
  color: $color
  font-weight: bold
  font-size: 16px

.timeline .action .action-header span.time
  float: right
  font-size: 12px

.timeline .action .action-content
  position: relative

.timeline .action .action-content p.message
  margin: 0

.timeline .action .action-content div.subject > label, .timeline .action .action-content div.from > label, .timeline .action .action-content div.to > label
  display: inline-block
  margin-bottom: 0
  padding-right: 5px
  width: 60px
  text-align: left
  font-weight: normal

.timeline .action .action-content div.subject > span, .timeline .action .action-content div.from > span, .timeline .action .action-content div.to > span
  display: inline-block

.timeline .action .action-content label.source-info
  display: block
  margin-bottom: 0
  font-weight: normal

.timeline .action .action-content label.task-info
  display: block
  margin-bottom: 0
  color: $color
  text-align: right
  font-weight: normal
  font-size: 12px
  .task-status
    display: inline-block
    width: 12px
    height: 12px
    border: 1px solid $color
    border-radius: 2px
  .task-status.task-status-completed:before
    display: inline-block
    color: $color
    content: "\f00c"
    font: normal normal normal 14px/1 FontAwesome
    font-size: inherit
    font-size: inherit


    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    text-rendering: auto

.timeline .action .action-content div.twitter-info .twitter-info-avatar
  float: left
  padding: 5px

.timeline .action .action-content div.twitter-info .twitter-info-avatar img
  width: 36px
  height: 36px
  border-radius: 4px

.timeline .action .action-content div.twitter-info .twitter-info-header h4.twitter-info-fullname
  display: inline-block
  margin: 0
  color: #666666
  font-weight: bold
  font-size: 14px

.timeline .action .action-content div.twitter-info .twitter-info-header span.twitter-info-username
  display: inline-block
  margin-left: 5px

.timeline .action .action-content div.action-buttons
  position: absolute
  top: 0
  right: 0

.timeline .action .action-content div.action-buttons a
  font-size: 14px

.timeline .action .action-content div.action-buttons a, .timeline .action .action-content div.action-buttons a:hover, .timeline .action .action-content div.action-buttons a:active
  color: #999999

.timeline .action .action-content div.action-buttons a:not(:first-child)
  margin-left: 5px

.timeline .action .action-footer
  position: absolute
  top: 100%
  left: 0
  width: 100%
  height: 60px
  border-bottom: 1px solid #f0f0f0

.timeline .action .action-footer h4.leadname
  position: absolute
  top: 15px
  left: 10px
  margin-top: 5px
  color: #666666
  font-size: 12px
  line-height: 20px

.timeline .action .action-footer h4.leadname .avatar
  display: inline-block

.timeline .action .action-footer h4.leadname .avatar img
  width: 20px
  height: 20px
  border-radius: 50%
  vertical-align: bottom

.timeline .action .action-footer h4.username
  position: absolute
  top: 15px
  right: 10px
  margin: 0
  margin-top: 5px
  color: #666666
  font-weight: normal
  font-size: 10px

.timeline .action .action-footer h4.username .avatar
  display: inline-block

.timeline .action .action-footer h4.username .avatar img
  width: 16px
  height: 16px
  border-radius: 50%
  vertical-align: bottom

.timeline .action .mark
  position: absolute
  top: 0
  left: -40px
  display: inline-block
  padding: 0
  width: 27px
  height: 100%
  background-color: transparent
  text-align: center

.timeline .action + .action .mark:before
  position: absolute
  bottom: 100%
  left: 13px
  width: 1px
  height: 100px
  background: rgba(240, 240, 240, 1)
  content: " "

.timeline .action .mark:after
  position: absolute
  top: 28px
  bottom: 0
  left: 13px
  width: 1px
  background: rgba(240, 240, 240, 1)
  content: " "

.timeline .action .mark .icon
  display: inline-block
  width: 27px
  height: 27px
  border: 1px solid #34a2cc
  border-radius: 50%
  background: #34a2cc
  color: #ffffff
  line-height: 27px

.timeline .action .action-form
  margin-top: -35px
  margin-bottom: 40px
  border: 1px solid #cecece
  border-radius: 0 0 6px 6px
  background-color: #f1f1f1

.timeline .action .action-form:before
  width: 100%
  border-top: 1px solid #cecece
  content: " "

.timeline .action form .form-group
  margin-bottom: 0

.timeline .action form .row
  margin: 5px 2.5px

.timeline .action form .col-md-2, .timeline .action form .col-md-3, .timeline .action form .col-md-4, .timeline .action form .col-md-6, .timeline .action form .col-md-10, .timeline .action form .col-md-12
  padding: 0 2.5px

.timeline .action form .bootstrap-text-editor
  overflow: auto
  padding: 15px
  height: 120px
  border: none
  border-bottom: 1px solid #cecece
  border-radius: 0

.timeline .action form .btn-toolbar .btn
  border-color: #cccccc
  background: #f1f1f1
  color: #999999

.timeline .action form .btn-toolbar .btn.btn-info
  background: #f9f9f9

// .timeline .action form.mail-action-form

.timeline .action form.mail-action-form .mail-to-wrapper
  position: relative

.timeline .action form.mail-action-form input.mail-to
  padding-right: 60px

.timeline .action form.mail-action-form .btn-cc-bcc-toggle
  position: absolute
  top: 2px
  right: 2px

.timeline .action form.mail-action-form .cc-bcc-wrapper
  overflow: hidden
  margin: 0 2.5px
  height: 0
  opacity: 0
  transition: 0.5s all

.timeline .action form.mail-action-form.show-cc-bcc .cc-bcc-wrapper
  height: 39px
  opacity: 1

.timeline .action form.mail-action-form .bootstrap-text-editor
  border-top: 1px solid #cecece
  transition: 0.5s all

.timeline .action form.mail-action-form.show-cc-bcc .bootstrap-text-editor
  height: 81px

.timeline .action .workflow-list-dropdown
  top: 140px
  right: auto
  left: 70px
  margin-top: 10px
  padding: 10px
  width: auto
  height: auto

.timeline .action .workflow-list-dropdown:before, .timeline .action .workflow-list-dropdown:after
  position: absolute
  bottom: 100%
  width: 0
  height: 0
  border: solid transparent
  content: " "
  pointer-events: none

.timeline .action .workflow-list-dropdown:before
  left: 10px
  border-width: 10px
  border-bottom-color: rgba(0, 0, 0, 0.15)

.timeline .action .workflow-list-dropdown:after
  left: 12px
  border-width: 8px
  border-bottom-color: #ffffff

.timeline .action .workflow-list-dropdown ul.workflow-list
  overflow-y: auto
  margin: 0
  padding: 0
  max-height: 435px
  list-style: none

.timeline .action .workflow-list-dropdown ul.workflow-list > li.workflow-list-item
  margin-bottom: 3px

.timeline .action .workflow-list-dropdown ul.workflow-list > li.workflow-list-item:last-child
  margin-bottom: 0

.timeline .action .workflow-list-dropdown ul.workflow-list > li.workflow-list-item > a
  display: block
  padding: 3px
  border-radius: 4px
  color: inherit
  text-decoration: none
  font-size: 16px
  cursor: pointer

.timeline .action .workflow-list-dropdown ul.workflow-list > li.workflow-list-item > a:hover, .timeline .action .workflow-list-dropdown ul.workflow-list > li.workflow-list-item.selected > a
  background-color: #0ab8ff
  color: #ffffff

.timeline .action .workflow-list-dropdown ul.workflow-list > li.workflow-list-item > a > i
  visibility: hidden
  font-size: 14px

.timeline .action .workflow-list-dropdown ul.workflow-list > li.workflow-list-item.selected > a > i
  visibility: visible

.timeline .action .twitter-message-form
  display: none

.timeline .action .twitter-message-form textarea
  border: #cecece solid
  border-width: 0
  border-bottom-width: 1px
  border-radius: 0

.timeline .action .twitter-message-form .twitter-message-username
  position: absolute
  padding: 6px 12px
  color: #00aaf0
  cursor: pointer

.timeline .action .twitter-message-form .twitter-message-form-buttons
  padding: 5px
  text-align: right

.timeline .action .twitter-message-form[data-event-type="twitter_direct"] .timeline .action .twitter-message-form-buttons span.left-letter-count
  display: none

.timeline .action .twitter-message-form .twitter-message-form-buttons button
  min-width: 80px

.timeline .action .twitter-message-form .twitter-message-form-buttons .btn-primary
  border: 0
  background-color: #34a2cc

.timeline .action .twitter-message-form .twitter-message-form-buttons .btn-cancel
  border: 1px #cecece solid
  background-color: #ffffff

.timeline .action .twitter-message-form .twitter-message-form-buttons .left-letter-count
  display: inline-block
  margin-right: 10px
  color: #9ca6b3
