#sidebar
  position: absolute
  top: 0
  right: 0
  bottom: 0
  z-index: 10
  border-left: 1px solid #cccccc
  background: #f2f2f2

#sidebar > ul.nav.nav-tabs.nav-stacked
  float: right
  width: 80px
  height: 100%
  border-bottom: none
  border-left: 1px solid #cccccc

#sidebar > ul.nav.nav-tabs.nav-stacked > li
  margin: 0
  border-bottom: 1px solid #cccccc
  color: #555555

#sidebar > ul.nav.nav-tabs.nav-stacked > li + li
  margin: 0

#sidebar > ul.nav.nav-tabs.nav-stacked > li > a
  margin: 0
  // font-size: 130%
  padding: 20px 3px 10px
  border-radius: 0
  color: #cccccc
  text-align: center
  font-weight: bold
  transition: all 0.4s

#sidebar > ul.nav.nav-tabs.nav-stacked > li.active > a, #sidebar > ul.nav.nav-tabs.nav-stacked > lii.active > a:focus, #sidebar > ul.nav.nav-tabs.nav-stacked > li.active > a:hover
  border: 1px solid transparent
  background-color: #cccccc
  color: #999999

#sidebar > ul.nav.nav-tabs.nav-stacked > li > a > i
  font-size: 250%

#sidebar > ul.nav.nav-tabs.nav-stacked > li > a > span.badge
  position: absolute
  top: 15px
  right: 15px
  // font-size: 150%
  padding: 0
  width: 20px
  height: 20px
  border-radius: 50%
  background: #d43f3a
  line-height: 20px

#sidebar > .tab-content
  visibility: hidden
  float: right
  overflow: hidden
  width: 500px
  width: 0
  height: 100%
  background: #ffffff
  transition: width 0.5s

#sidebar > .tab-content.open
  visibility: visible
  width: 500px

#sidebar > .tab-content.open:hover
  overflow-y: auto

#sidebar > .tab-content > .tab-pane
  position: relative
  padding: 15px 30px
  width: 500px
  height: 100%

#sidebar > .tab-content > .tab-pane .date-viewer
  position: relative
  margin-top: 10px
  text-align: center

#sidebar > .tab-content > .tab-pane .date-viewer:before, #sidebar > .tab-content > .tab-pane .date-viewer:after
  position: absolute
  top: 50%
  margin-top: -1px
  width: 30%
  height: 2px
  background-color: #cccccc
  content: " "

#sidebar > .tab-content > .tab-pane .date-viewer:before
  left: 0

#sidebar > .tab-content > .tab-pane .date-viewer:after
  right: 0

#sidebar > .tab-content > .tab-pane .date-viewer .date
  margin: auto
  width: 70px
  border: 1px solid #cccccc
  background: #ffffff
  text-align: center

#sidebar > .tab-content > .tab-pane .date-viewer .date .month
  display: block
  border-bottom: 1px solid #cccccc
  background-color: #34a2cc
  color: #ffffff
