// .contact-list

.contact-list .contact
  position: relative
  margin-bottom: 10px
  padding: 5px
  padding-left: 50px
  font-size: 12px

.contact-list.contact-list-from-search .contact
  padding-bottom: 10px
  padding-left: 100px
  border-bottom: 1px solid #efefef
  font-size: 14px

.contact-list:not(.contact-list-from-search) .contact
  background: rgba(248, 248, 248, 1)

.contact-list .contact .contact-avatar
  position: absolute
  top: 5px
  left: 5px
  float: left
  text-align: center

.contact-list .contact .contact-avatar img
  width: 36px
  height: 36px
  border-radius: 50%

.contact-list.contact-list-from-search .contact .contact-avatar img
  width: 80px
  height: 80px
  border-radius: 4px
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5)

.contact-list.contact-list-from-search .contact .contact-avatar .social-links
  margin-top: 5px
  text-align: center

.contact-list.contact-list-from-search .contact .contact-avatar .social-links a
  display: inline-block

.contact-list.contact-list-from-search .contact .contact-avatar .social-links a .fa
  width: 20px
  height: 20px
  border-radius: 50%
  color: #ffffff
  font-size: 80%
  line-height: 21px
  cursor: pointer

.contact-list.contact-list-from-search .contact .contact-avatar .social-links a .fa-cliently-facebook
  background-color: #3b5998

.contact-list.contact-list-from-search .contact .contact-avatar .social-links a .fa-cliently-twitter
  background-color: #0ab8ff

.contact-list.contact-list-from-search .contact .contact-avatar .social-links a .fa-cliently-linkedin
  background-color: #1991bf

.contact-list .contact .contact-info
  padding: 5px
  border-radius: 4px
  background: rgba(248, 248, 248, 1)

.contact-list.contact-list-from-search .contact .contact-info
  padding: 0
  padding-right: 50px
  background: #ffffff

.contact-list .contact .contact-info h3.contact-name
  margin: 0
  font-weight: 600
  font-size: 16px

.contact-list.contact-list-from-search .contact .contact-info h3.contact-name
  font-weight: 900
  font-size: 24px

.contact-list .contact .contact-info > span
  display: block

.contact-list .contact .action-buttons.btn-group a.btn, .contact-list .contact .action-buttons.btn-group a.btn:focus, .contact-list .contact .action-buttons.btn-group a.btn:active
  background: none
  color: #34a2cc

// .contact-list.contact-list-from-search .contact .contact-accepted-status
  position: absolute
  top: 50%
  right: 0
  margin-top: -32px
  width: 48px
  height: 48px
  border-radius: 50%
  background-color: #34a2cc
  color: white
  text-align: center
  font-size: 28px
  line-height: 52px

.contact-list .contact div.form-actions
  position: absolute
  top: 50%
  right: 0
  margin-top: -12px
  text-align: center

.contact-list .contact .form-actions .btn
  padding: 4px 8px
  width: 50px
  height: 24px
  font-size: 10px

.contact-list .contact .form-actions .btn-accept.btn-workflow-activation
  padding: 0 3px
  padding-bottom: 2px
  line-height: 1

.contact-list .contact .form-actions .btn-accept.btn-workflow-activation small
  font-size: 50%

.contact-list .contact .form-actions .btn-reject
  border: none
  background: none
  color: red
  text-decoration: underline

.contact-list .contact .action-form
  margin-bottom: 5px
  margin-left: -45px
  border: 1px solid #cecece
  border-radius: 0 0 6px 6px
  background-color: #f1f1f1

.contact-list .contact form .form-group
  margin-bottom: 0

.contact-list .contact form .row
  margin: 5px 2.5px

.contact-list .contact form .col-md-2, .contact-list .contact form .col-md-3, .contact-list .contact form .col-md-4, .contact-list .contact form .col-md-6, .contact-list .contact form .col-md-10, .contact-list .contact form .col-md-12
  padding: 0 2.5px

.contact-list .contact form .bootstrap-text-editor
  overflow: auto
  padding: 15px
  height: 120px
  border: none
  border-bottom: 1px solid #cecece
  border-radius: 0

.contact-list .contact form .btn-toolbar .btn
  border-color: #cccccc
  background: #f1f1f1
  color: #999999

.contact-list .contact form .btn-toolbar .btn.btn-info
  background: #f9f9f9

// .contact-list .contact form.mail-action-form

.contact-list .contact form.mail-action-form .mail-to-wrapper
  position: relative

.contact-list .contact form.mail-action-form input.mail-to
  padding-right: 60px

.contact-list .contact form.mail-action-form .btn-cc-bcc-toggle
  position: absolute
  top: 2px
  right: 2px

.contact-list .contact form.mail-action-form .cc-bcc-wrapper
  overflow: hidden
  margin: 0 2.5px
  height: 0
  opacity: 0
  transition: 0.5s all

.contact-list .contact form.mail-action-form.show-cc-bcc .cc-bcc-wrapper
  height: 39px
  opacity: 1

.contact-list .contact form.mail-action-form .bootstrap-text-editor
  border-top: 1px solid #cecece
  transition: 0.5s all

.contact-list .contact form.mail-action-form.show-cc-bcc .bootstrap-text-editor
  height: 81px

.contact-list .contact .workflow-list-dropdown
  top: 140px
  right: auto
  left: 70px
  margin-top: 10px
  padding: 10px
  width: auto
  height: auto

.contact-list .contact .workflow-list-dropdown:before, .contact-list .contact .workflow-list-dropdown:after
  position: absolute
  bottom: 100%
  width: 0
  height: 0
  border: solid transparent
  content: " "
  pointer-events: none

.contact-list .contact .workflow-list-dropdown:before
  left: 10px
  border-width: 10px
  border-bottom-color: rgba(0, 0, 0, 0.15)

.contact-list .contact .workflow-list-dropdown:after
  left: 12px
  border-width: 8px
  border-bottom-color: #ffffff

.contact-list .contact .workflow-list-dropdown ul.workflow-list
  overflow-y: auto
  margin: 0
  padding: 0
  max-height: 435px
  list-style: none

.contact-list .contact .workflow-list-dropdown ul.workflow-list > li.workflow-list-item
  margin-bottom: 3px

.contact-list .contact .workflow-list-dropdown ul.workflow-list > li.workflow-list-item:last-child
  margin-bottom: 0

.contact-list .contact .workflow-list-dropdown ul.workflow-list > li.workflow-list-item > a
  display: block
  padding: 3px
  border-radius: 4px
  color: inherit
  text-decoration: none
  font-size: 16px
  cursor: pointer

.contact-list .contact .workflow-list-dropdown ul.workflow-list > li.workflow-list-item > a:hover, .contact-list .contact .workflow-list-dropdown ul.workflow-list > li.workflow-list-item.selected > a
  background-color: #0ab8ff
  color: #ffffff

.contact-list .contact .workflow-list-dropdown ul.workflow-list > li.workflow-list-item > a > i
  visibility: hidden
  font-size: 14px

.contact-list .contact .workflow-list-dropdown ul.workflow-list > li.workflow-list-item.selected > a > i
  visibility: visible

.contact-list .contact .twitter-message-form
  display: none

.contact-list .contact .twitter-message-form textarea
  border: #cecece solid
  border-width: 0
  border-bottom-width: 1px
  border-radius: 0

.contact-list .contact .twitter-message-form .twitter-message-username
  position: absolute
  padding: 6px 12px
  color: #00aaf0
  cursor: pointer

.contact-list .contact .twitter-message-form .twitter-message-form-buttons
  padding: 5px
  text-align: right

.contact-list .contact .twitter-message-form[data-event-type="twitter_direct"] .contact-list .contact .twitter-message-form-buttons span.left-letter-count
  display: none

.contact-list .contact .twitter-message-form .twitter-message-form-buttons button
  min-width: 80px

.contact-list .contact .twitter-message-form .twitter-message-form-buttons .btn-primary
  border: 0
  background-color: #34a2cc

.contact-list .contact .twitter-message-form .twitter-message-form-buttons .btn-cancel
  border: 1px #cecece solid
  background-color: #ffffff

.contact-list .contact .twitter-message-form .twitter-message-form-buttons .left-letter-count
  display: inline-block
  margin-right: 10px
  color: #9ca6b3

.contact-list.contact-list-from-search .contact .contact-accepted-status i.fa:before
  content: "\f023"

.contact-list.contact-list-from-search .contact.contact-accepted .contact-accepted-status i.fa:before
  content: "\f13e"
