.onoffswitch
  position: relative
  width: 36px


  -webkit-user-select: none
  -moz-user-select: none
  -ms-user-select: none

.onoffswitch.onoffswitch-vertical
  width: auto
  height: 36px

.onoffswitch-checkbox
  display: none

.onoffswitch .onoffswitch-label
  display: block
  overflow: hidden
  padding: 0
  height: 16px
  border: 2px solid #cccccc
  border-radius: 16px
  background-color: #ffffff
  line-height: 16px
  cursor: pointer
  transition: background-color 0.3s ease-in

.onoffswitch.onoffswitch-vertical .onoffswitch-label
  width: 16px
  height: 100%

.onoffswitch-label:before
  position: absolute
  top: 0
  right: 20px
  bottom: 0
  display: block
  margin: 0
  width: 16px
  border: 2px solid #cccccc
  border-radius: 16px
  background: #ffffff
  content: ""
  transition: all 0.3s ease-in 0s

.onoffswitch.onoffswitch-vertical .onoffswitch-label:before
  top: auto
  right: 0
  bottom: 20px
  left: 0
  width: auto
  height: 16px

.onoffswitch-checkbox:checked + .onoffswitch-label
  background-color: #49e845

.onoffswitch-checkbox:checked + .onoffswitch-label, .onoffswitch-checkbox:checked + .onoffswitch-label:before
  border-color: #49e845

.onoffswitch-checkbox:checked + .onoffswitch-label:before
  right: 0

.onoffswitch.onoffswitch-vertical .onoffswitch-checkbox:checked + .onoffswitch-label:before
  bottom: 0

