a.social-integration-handler
  position: relative
  display: inline-block

a.social-integration-handler.empty, a.social-integration-handler.empty:active, a.social-integration-handler.empty:hover
  color: #dd1144
  font-style: italic

a.social-integration-handler span.icon i.fa-cliently-twitter
  margin-right: 4px
  width: 25px
  height: 25px
  border-radius: 50%
  background-color: #cccccc
  color: #ffffff
  text-align: center
  line-height: 25px

a.social-integration-handler.active span.icon i.fa-cliently-twitter
  background-color: #00aaf0

a.social-integration-handler span.icon img
  width: 20px

a.social-integration-handler span.icon span.text
  display: none
  margin-left: 5px

a.social-integration-handler span.icon span.checked
  display: none

a.social-integration-handler label
  font-weight: normal
