// Common Values Start
$color: #00aaf0

// Common Values End

// Redefinitions of Standard Tags Start
// Fonts
@font-face
  font-family: "ProximaNova"
  src: url("../font/proximanova-regular-webfont.woff") format("woff")


@font-face
  font-family: "ProximaNova Semibold"
  src: url("../font/proximanova-semibold-webfont.woff") format("woff")


body
  background-color: #f2f2f2
  font-family: "ProximaNova", sans-serif !important

// h1, h2, h3, h4, h5, h6
  font-family: 'ProximaNova Semibold', sans-serif !important

// Redefinitions of Standard Tags End

// Global Elements Start
.back
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: 100%
  background: #000000
  opacity: 0.4

#global-loader-back
  z-index: 10000
  background: #ffffff
  opacity: 0.75

#global-loader
  position: absolute
  top: 50%
  left: 50%
  z-index: 10001
  margin-top: -50px
  margin-left: -50px
  color: $color

#global-loader-text
  position: absolute
  top: 50%
  left: 50%
  z-index: 10001
  margin-top: 60px
  margin-left: -200px
  width: 400px
  color: $color
  text-align: center
  font-size: 20px

.pac-container
  z-index: 10000 !important
  width: 320px !important

.textarea-message[contenteditable=true]:empty:before
  // For Firefox
  display: block
  content: attr(placeholder)

\::-webkit-scrollbar
  width: 10px
  height: 10px

\::-webkit-scrollbar-button
  display: none

\::-webkit-scrollbar-track-piece
  // border-radius: 5px
  background: rgba(0, 0, 0, 0.1)

\::-webkit-scrollbar-thumb
  border-radius: 5px
  background: rgba(0, 0, 0, 0.4)

// \::-webkit-scrollbar-track

// Global Elements End

// Plugins Start
@import plugins/x-editable-address2
@import plugins/switch
@import plugins/inputfile

// Plugins End

@import layout
@import components

