.mail-list
  position: relative
  margin-top: 30px
  color: #999999

.mail-list .mail-content
  position: relative
  padding: 15px
  border: 1px solid #cecece
  border-radius: 4px
  background: #ffffff

.mail-list .mail-content:before, .mail-list .mail-content:after
  position: absolute
  top: 33px
  right: 100%
  width: 0
  height: 0
  border: solid transparent
  content: " "
  pointer-events: none

.mail-list .mail-content:after
  margin-top: -10px
  border-width: 10px
  border-color: transparent
  border-right-color: #ffffff

.mail-list .mail-content:before
  margin-top: -12px
  border-width: 12px
  border-color: transparent
  border-right-color: #cecece

.mail-list .mail .date-wrapper
  float: right
  color: #34a2cc
  font-size: 12px

.mail-list .mail h3
  margin-top: 0
  font-weight: 600
  font-size: 16px

.mail-list .mail span.mail-from, .mail-list .mail span.mail-to, .mail-list .mail span.mail-cc, .mail-list .mail span.mail-bcc
  color: #34a2cc

.mail-list .mail .mail-description
  overflow-x: auto
  padding: 10px 0
  white-space: pre-wrap

.mail-list .mail .action-buttons
  margin-top: 5px

.mail-list .mail .action-buttons a, .mail-list .mail .action-buttons a:hover
  margin-right: 10px
  color: #343c3f
  text-decoration: none
  font-weight: 500
  cursor: pointer

.mail-list .mail .mark
  position: absolute
  top: 20px
  left: -40px
  display: inline-block
  padding: 0
  width: 27px
  height: 100%
  background-color: transparent
  text-align: center

.mail-list .mail:not(:last-child) .mark:after
  position: absolute
  top: 28px
  left: 13px
  width: 1px
  height: 100%
  background: #cecece
  content: " "

.mail-list .mail .mark .icon
  display: inline-block
  width: 27px
  height: 27px
  border: 1px solid #cecece
  border-radius: 50%
  background: #ffffff
  color: #34a2cc
  line-height: 29px

.mail-list .mail > .mail-action-form
  margin-top: 10px

.mail-list .mail > .mail-action-form button.btn-cancel
  float: right
  margin-left: 5px
  border: 0
