.note-list
  position: relative
  margin-top: 30px
  color: #999999

.note-list .note-content
  position: relative
  padding: 15px
  border: 1px solid #cecece
  border-radius: 4px
  background: #ffffff

.note-list .note-content:before, .note-list .note-content:after
  position: absolute
  top: 33px
  right: 100%
  width: 0
  height: 0
  border: solid transparent
  content: " "
  pointer-events: none

.note-list .note-content:after
  margin-top: -10px
  border-width: 10px
  border-color: transparent
  border-right-color: #ffffff

.note-list .note-content:before
  margin-top: -12px
  border-width: 12px
  border-color: transparent
  border-right-color: #cecece

.note-list .note [data-name]
  color: #343c3f
  transition: 0.5s color

.note-list .note .date-wrapper
  float: right
  color: #343c3f
  font-size: 12px

.note-list .note h3
  margin-top: 0
  font-weight: 600
  font-size: 16px

.note-list .note .note-description
  overflow-x: auto
  padding: 10px 0
  white-space: pre-wrap

.note-list .note .mark
  position: absolute
  top: 20px
  left: -40px
  display: inline-block
  padding: 0
  width: 27px
  height: 100%
  background-color: transparent
  text-align: center

.note-list .note:not(:last-child) .mark:after
  position: absolute
  top: 28px
  left: 13px
  width: 1px
  height: 100%
  background: #cecece
  content: " "

.note-list .note .mark .icon
  display: inline-block
  width: 27px
  height: 27px
  border: 1px solid #cecece
  border-radius: 50%
  background: #ffffff
  color: #34a2cc
  line-height: 29px
