body
  width: 100%

body.page-header-fixed
  overflow: hidden

.page-sidebar
  position: fixed
  top: 0
  left: 0
  z-index: 100
  width: 80px
  height: 100%
  background-color: #111111
  color: #6f7b7f
  transition: width 0.4s
  .page-logo
    margin-top: 16px
    cursor: pointer
    img
      display: block
      margin: auto
      width: 41px
  ul.workspace-list
    display: none
    margin: 0
    margin-top: 20px
    padding: 0
    list-style: none
    li.workspace
      padding: 10px
      color: #ffffff
      opacity: 0.5
      cursor: pointer
      transition: 0.4s opacity
      &.active
        opacity: 1
      img
        height: 24px
      label
        margin-top: 10px
        font-weight: normal
        line-height: 1
        cursor: inherit
    li.workspace:hover, li.page-menu-item.active
      opacity: 1
  ul.page-menu
    margin: 0
    margin-top: 55px
    padding: 0
    list-style: none
    li.page-menu-item
      padding: 10px
      width: 100%
      color: #ffffff
      text-align: center
      opacity: 0.5
      cursor: pointer
      transition: 0.4s opacity
      img
        height: 28px
      label
        display: block
        margin-top: 10px
        font-weight: normal
        line-height: 1
        cursor: inherit
    li.page-menu-item:hover, li.page-menu-item.active
      opacity: 1
    li.page-menu-item.page-menu-item-clients
      img
        height: 36px
    li.page-menu-item.page-menu-item-search
      img
        height: 36px
    li.page-menu-item.page-menu-item-settings
      position: absolute
      bottom: 10px
  .workspace-name
    margin-top: 0
    padding-top: 20px
    color: #ffffff
    text-align: center
    font-size: 14px
    cursor: pointer

body.page-sidebar-expanded
  .page-sidebar
    // width: 160px
    ul.workspace-list
      display: block
      border-radius: 8px
      background-color: #222222
      li.workspace
        padding: 0 5px
        border-bottom: 2px solid #111111
        text-align: center
        &:last-child
          border-bottom: none
  li.page-menu-item
    text-align: left

.page-container-wrapper
  position: absolute
  top: 0
  left: 0
  padding-left: 80px
  width: 100%
  height: 100%
  transition: padding-left 0.8s
  .page-container
    position: relative
    padding-top: 80px
    height: 100%
    .page-container-inner
      height: 100%
      .page-header
        position: absolute
        top: 0
        left: 0
        margin: 0
        padding: 20px
        padding-top: 22px
        width: 100%
        height: 80px
        border-bottom: 1px solid #cecece
        background-color: #ffffff
        .page-quick-searchbar
          float: left
          max-width: 41.66666667%
        .top-menu
          float: right
          margin-top: -4px
          .nav.navbar-nav.navbar-right
            margin-right: 0
            > li
              a
                margin: 0 10px
                padding: 0
                img#user-profile-image
                  display: none
                  width: 48px
                  height: 48px
                  border-radius: 50%
              a
                &:focus, &:hover
                  background: none
              .dropdown-menu
                margin-top: 10px
                padding: 0
                li
                  transition: all 0.3s ease
                  a
                    padding: 8px 15px !important
                li
                  &:hover
                    background: #e0e0e0
              .dropdown-menu:after, .dropdown-menu:before
                position: absolute
                bottom: 100%
                left: 73%
                width: 0
                height: 0
                border: solid transparent
                content: " "
                pointer-events: none
              .dropdown-menu:after
                top: -16px
                margin-left: -8px
                border-width: 8px
                border-color: rgba(136, 183, 213, 0)
                border-bottom-color: #ffffff
              .dropdown-menu:before
                top: -20px
                margin-left: -10px
                border-width: 10px
                border-color: rgba(194, 225, 245, 0)
                border-bottom-color: #cccccc
            > li:not(:last-child)
              margin: 0
              height: 48px
              border-right: 1px solid #cccccc
            li.plan-upgrade-recommender
              // display: none
              a
                margin-top: 15px
                color: #bf1c56
            li.available-deal-counter
              a
                display: inline-block
                margin-top: 6px
                padding: 10px 10px
                min-width: 36px
                min-height: 36px
                border-radius: 50%
                background-color: orange
                color: #ffffff
                text-align: center
                white-space: nowrap
            li.bell-li
              margin-top: 12px
              padding: 2px
            li.add-lead-source-li
              // display: none
              a
                margin-top: 5px
                padding: 8px 10px
                border-radius: 7px
                background: $color
                color: #ffffff
                font-weight: 500
                transition: all 0.3s ease
              a
                &:hover
                  background: #008fcc
      .page-content
        height: 100%
        .main-block-wrapper
          position: relative
          height: 100%
          .main-block
            position: relative
            // padding-top: 52px
            height: 100%
            .main-header
              // position: absolute
              // top: 0
              // left: 0
              padding: 10px 30px
              padding-right: 0
              padding-bottom: 0
              width: 100%
              .main-block-tools
                margin-top: 1em
                text-align: right
                > div
                  float: right
              h1.page-title
                margin: 0
                color: #343c3f
                font-weight: 400
                font-size: 24px
            .main-content
              padding: 10px
              height: 100%
              .columns-container
                overflow: hidden
                overflow-x: auto
                padding-bottom: 10px
                width: 100%
                height: 100%
                .columns
                  width: 100%
                  height: 100%
                  .column
                    float: left
                    width: 20%
                    height: 100%
                    .column-inner
                      position: relative
                      height: 100%

body:not([data-page="clients"])
  .page-quick-searchbar
    display: none
