body.settings-page-sidebar-open
  .page-container-wrapper
    padding-left: 380px

.settings-page-sidebar
  position: fixed
  top: 0
  left: 80px
  visibility: hidden
  padding: 2px 40px
  width: 300px
  height: 100%
  background-color: #191919
  transition: all 0.8s
  -webkit-transform: translate3d(-100%, 0, 0)
  transform: translate3d(-100%, 0, 0)

body.settings-page-sidebar-open
  .settings-page-sidebar
    visibility: visible
    -webkit-transform: translate3d(0, 0, 0)
    transform: translate3d(0, 0, 0)

.settings-page-sidebar
  .head-section h1
    margin-bottom: 0
    padding-bottom: 15px
    border-bottom: 1px solid #7c7e7f
    color: #ffffff
    font-weight: 400
    font-size: 24px
  .head-section h2
    margin-bottom: 25px
    padding-bottom: 15px
    border-bottom: 1px solid #7c7e7f
    color: #7c7e7f
    font-weight: 400
    font-size: 14px
  ul.settings-page-menu
    margin-left: 0
    padding: 0
    list-style: none
    li
      a
        display: block
        padding: 5px 0
        color: #7c7e7f
        text-decoration: none
        transition: all 0.4s ease
        \:hover, :focus
          color: #ffffff
    li.active
      a
        color: #ffffff

