.twitter-item
  position: relative
  margin-bottom: 20px
  margin-left: 35px

.twitter-item .twitter-item-inner
  position: relative
  padding: 15px
  border: 1px solid #cecece
  border-radius: 4px
  background: #ffffff

.twitter-item .twitter-item-info
  margin-bottom: 5px
  padding-top: 8px
  padding-left: 64px
  min-height: 56px

.twitter-item img.twitter-item-profile-image
  float: left
  height: 56px
  border-radius: 10px

.twitter-item button.twitter-item-button-follow
  float: right
  margin-top: 8px
  margin-right: 2px
  min-width: 100px
  background-color: #00aaf0
  color: #ffffff
  font-weight: 600
  transition: all 0.4s ease

.twitter-item button.twitter-item-button-follow:hover
  background-color: #0081b8

#client-details-twitter-info.following .twitter-item button.twitter-item-button-follow
  border: 1px solid #00aaf0
  background-color: #ffffff
  color: #00aaf0

#client-details-twitter-info.following .twitter-item button.twitter-item-button-follow:hover
  background-color: #00aaf0
  color: #ffffff

.twitter-item h4.fullname
  margin: 0
  color: #333333

.twitter-item .twitter-item-description
  color: #333333
  font-size: 90%

.twitter-item .twitter-item-metrics
  margin-top: 5px
  color: #999999

// .twitter-item .twitter-item-buttons

.twitter-item .twitter-item-buttons a.twitter-item-button
  margin-right: 50px
  color: #9ca6b3
  font-size: 120%

.twitter-item .twitter-item-buttons a.twitter-item-button[data-event-type="twitter_direct"]
  color: #00aaf0

.twitter-item .twitter-item-buttons a.twitter-item-button[data-event-type="twitter_direct"][disabled="disabled"]
  color: #9ca6b3
  opacity: 0.6
  cursor: default
  pointer-events: none

.twitter-item .twitter-message-form
  display: none

.twitter-item .twitter-message-form textarea
  border: #cecece solid
  border-width: 1px 0
  border-radius: 0

.twitter-message-username
  position: absolute
  padding: 6px 12px
  color: #00aaf0
  cursor: pointer

.twitter-item .twitter-message-form .twitter-message-form-buttons
  padding: 5px
  text-align: right

.twitter-item .twitter-message-form[data-event-type="twitter_direct"] .twitter-item .twitter-message-form-buttons span.left-letter-count
  display: none

.twitter-item .twitter-message-form .twitter-message-form-buttons button
  min-width: 80px

.twitter-item .twitter-message-form .twitter-message-form-buttons .btn-primary
  border: 0
  background-color: #34a2cc

.twitter-item .twitter-message-form .twitter-message-form-buttons .btn-cancel
  border: 1px #cecece solid
  background-color: #ffffff

.twitter-item .twitter-message-form .twitter-message-form-buttons .left-letter-count
  display: inline-block
  margin-right: 10px
  color: #9ca6b3

.twitter-item-list
  position: relative
  margin-top: 30px
  color: #999999

.twitter-item-list > .twitter-item.mine .twitter-item-inner
  background-color: #f4f8f9

.twitter-item-list > .twitter-item .twitter-item-inner:before, .twitter-item-list > .twitter-item .twitter-item-inner:after
  position: absolute
  top: 33px
  right: 100%
  width: 0
  height: 0
  border: solid transparent
  content: " "
  pointer-events: none

.twitter-item-list > .twitter-item .twitter-item-inner:after
  margin-top: -10px
  border-width: 10px
  border-color: transparent
  border-right-color: #ffffff

.twitter-item-list > .twitter-item.mine .twitter-item-inner:after
  border-right-color: #f4f8f9

.twitter-item-list > .twitter-item .twitter-item-inner:before
  margin-top: -12px
  border-width: 12px
  border-color: transparent
  border-right-color: #cecece

.twitter-item-list > .twitter-item .date-wrapper
  float: right
  color: #34a2cc
  font-size: 12px

.twitter-item-list > .twitter-item .twitter-item-info
  padding-top: 0

.twitter-item-list > .twitter-item.has-notice .twitter-item-list > .twitter-item-info
  padding-top: 8px

.twitter-item-list > .twitter-item h4.fullname
  display: inline-block

.twitter-item-list > .twitter-item .twitter-item-notice
  // font-size: 120%
  color: #00aaf0

.twitter-item-list > .twitter-item .twitter-item-notice h4.fullname
  font-size: inherit

.twitter-item-list > .twitter-item .twitter-item-metainfo
  // position: absolute
  // top: 10px
  // right: 10px
  float: right
  color: #00aaf0
  // margin: 10px 0
  font-size: 80%

.twitter-item-list > .twitter-item .twitter-item
  overflow: hidden
  margin: 20px
  height: auto
  border: 1px solid #cfcfcf
  border-radius: 4px

.twitter-item-list > .twitter-item .twitter-item .twitter-item-list > .twitter-item-info
  margin-bottom: 0
  padding-top: 0
  padding-left: 0
  min-height: 0

.twitter-item-list > .twitter-item .twitter-message-form
  position: relative
  overflow: hidden
  margin-top: 5px
  border: 1px solid #cfcfcf
  border-radius: 4px

.twitter-item-list > .twitter-item .twitter-message-form textarea
  border-top: 0

.twitter-item-list > .twitter-item .mark
  position: absolute
  top: 20px
  left: -40px
  display: inline-block
  padding: 0
  width: 27px
  height: 100%
  background-color: transparent
  text-align: center

.twitter-item-list > .twitter-item:not(:last-child) .mark:after
  position: absolute
  top: 28px
  left: 13px
  width: 1px
  height: 100%
  background: #cecece
  content: " "

.twitter-item-list > .twitter-item .mark .icon
  display: inline-block
  width: 27px
  height: 27px
  border: 1px solid #cecece
  border-radius: 50%
  background: #ffffff
  color: #34a2cc
  line-height: 29px

.twitter-item-list > .twitter-item .mark .icon i:before
  content: "\f099"

.twitter-item-list > .twitter-item[data-event-type="twitter_reply"] .mark .icon i:before
  content: "\f112"

.twitter-item-list > .twitter-item[data-event-type="twitter_retweet"] .mark .icon i:before
  content: "\f079"

.twitter-item-list > .twitter-item[data-event-type="twitter_follow"] .mark .icon i:before, .twitter-item-list > .twitter-item[data-event-type="twitter_unfollow"] .mark .icon i:before, .twitter-item-list > .twitter-item[data-event-type="twitter_favorite"] .mark .icon i:before
  content: "\f234"
