#user-connector-wizard.modal .modal-dialog
  width: 640px

#user-connector-wizard.modal .modal-dialog .modal-header
  padding-top: 30px
  border-bottom: none
  background-color: initial
  color: initial
  text-align: center

#user-connector-wizard.modal .modal-dialog .modal-header h4
  color: #333333
  font-size: 24px

#user-connector-wizard.modal .modal-dialog .modal-header h5
  color: #666666
  font-weight: 600

#user-connector-wizard.modal .modal-dialog .modal-body
  padding-top: 0

#user-connector-wizard.modal .modal-dialog .modal-footer
  padding-bottom: 30px
  border-top: none
  text-align: center

#user-connector-wizard.modal .modal-dialog .modal-footer button.btn-save
  min-width: 100px

#user-connector-wizard.modal .modal-dialog .modal-footer a.btn-save
  color: #00aaf0
  text-decoration: underline
  font-weight: bold

#user-connector-wizard.modal .modal-dialog .modal-footer a.btn-save + span.skip-note
  color: #cccccc
  font-size: 90%

// #user-connector-wizard.modal ul.statuses
  display: none
  margin: 0 auto
  padding: 15px 0
  width: 330px
  height: 65px
  list-style: none
  text-align: center

// #user-connector-wizard.modal ul.statuses li.status
  position: relative
  float: left
  width: 20px
  height: 20px
  text-align: left
  transition: all 0.4s

// #user-connector-wizard.modal ul.statuses li.status:not(:last-child)
  width: 155px

// #user-connector-wizard.modal ul.statuses li.status:not(:last-child):before
  position: absolute
  top: 9px
  left: 20px
  width: 135px
  height: 2px
  background: #d3d6d7
  content: " "

// #user-connector-wizard.modal ul.statuses li.status:not(:last-child):after
  position: absolute
  top: 9px
  left: 20px
  width: 0
  height: 2px
  background: #d3d6d7
  content: " "
  transition: all 0.5s

// #user-connector-wizard.modal ul.statuses li.status .point
  position: relative
  z-index: 1
  display: inline-block
  width: 20px
  height: 20px
  border: 1px solid #d3d6d7
  border-radius: 50%
  background: #ffffff
  transition: all 0.4s

// #user-connector-wizard.modal ul.statuses li.status .point:before
  position: absolute
  top: 4px
  left: 4px
  width: 10px
  height: 10px
  border-radius: 50%
  background: #d3d6d7
  content: " "

// #user-connector-wizard.modal ul.statuses li.status.current .point
  border-color: #34a2cc

// #user-connector-wizard.modal ul.statuses li.status.current .point:before
  background: #34a2cc

// #user-connector-wizard.modal ul.statuses li.status.current .point:after
  background: #34a2cc

// #user-connector-wizard.modal ul.statuses li.status.completed:after
  width: 135px
  background: #34a2cc

// #user-connector-wizard.modal ul.statuses li.status.completed .point
  border-color: #34a2cc
  background-color: #34a2cc

// #user-connector-wizard.modal ul.statuses li.status.completed .point:before
  background: #34a2cc

// #user-connector-wizard.modal ul.statuses li.status.completed .point:after
  background: #34a2cc

// #user-connector-wizard.modal ul.statuses li.status span.point span.title
  position: absolute
  top: 100%
  left: 50%
  margin-top: 10px
  white-space: nowrap

// #user-connector-wizard.modal ul.statuses li.status[data-step="getting-started"] span.point span.title
  margin-left: -45px

// #user-connector-wizard.modal ul.statuses li.status[data-step="gmail-connect"] span.point span.title
  margin-left: -15px

// #user-connector-wizard.modal ul.statuses li.status[data-step="social"] span.point span.title
  margin-left: -15px

#user-connector-wizard.modal .user-connector-wizard-step
  position: relative
  display: none
  clear: both
  overflow: hidden
  padding: 15px 50px
  padding-top: 0
  height: 400px
  text-align: center

#user-connector-wizard.modal .user-connector-wizard-step[data-step="getting-started"] iframe
  max-width: 100%
  width: 428px
  height: 240px

#user-connector-wizard.modal .social-integration-handler-container
  margin-top: 160px

#user-connector-wizard.modal .social-integration-handler-container a.social-integration-handler.empty, #user-connector-wizard.modal .social-integration-handler-container a.social-integration-handler.empty:active #user-connector-wizard.modal .social-integration-handler-container a.social-integration-handler.empty:hover
  font-style: normal

#user-connector-wizard.modal .user-connector-wizard-step .social-integration-handler-container a.social-integration-handler
  margin-bottom: 15px
  padding-top: 4px
  width: 200px
  border-radius: 4px
  background-color: #dc3e31
  color: #ffffff
  text-align: center
  line-height: 52px

#user-connector-wizard.modal .user-connector-wizard-step .social-integration-handler-container a.social-integration-handler label.title
  display: none

#user-connector-wizard.modal .user-connector-wizard-step[data-step="twitter-connect"] .social-integration-handler-container a.social-integration-handler
  background-color: #00aaf0

#user-connector-wizard.modal .user-connector-wizard-step .social-integration-handler-container a.social-integration-handler > i
  font-size: 20px

#user-connector-wizard.modal .user-connector-wizard-step .social-integration-handler-container a.social-integration-handler span.icon > i
  position: absolute
  top: 10px
  left: 23px
  background: none
  font-size: 36px
  line-height: 36px

#user-connector-wizard.modal .user-connector-wizard-step .social-integration-handler-container a.social-integration-handler span.icon > img
  position: absolute
  top: 15px
  left: 23px
  width: 32px

#user-connector-wizard.modal .user-connector-wizard-step .social-integration-handler-container a.social-integration-handler.active span.icon span.checked
  position: absolute
  top: 0
  left: 50%
  display: block
  margin-top: -12px
  margin-left: -12px
  width: 25px
  height: 25px
  border-radius: 50%
  background: green
  color: #ffffff
  text-align: center
  line-height: 27px

#user-connector-wizard.modal .user-connector-wizard-step .social-integration-handler-container a.social-integration-handler span.icon span.text
  display: inline-block
  margin-left: 30px
  color: white
  font-weight: bold
  font-size: 16px
  line-height: 48px

#user-connector-wizard.modal .user-connector-wizard-step .social-integration-note
  color: #999999
  font-size: 90%

#user-connector-wizard.modal .user-connector-wizard-step[data-step="twitter-tweet"] form
  margin-top: 50px
  padding: 0 50px
  text-align: left

#user-connector-wizard.modal .user-connector-wizard-step[data-step="twitter-tweet"] form .form-group
  margin-bottom: 8px

#user-connector-wizard.modal .user-connector-wizard-step[data-step="twitter-tweet"] form label.handle
  color: #0ab8ff
  font-weight: 500

#user-connector-wizard.modal .user-connector-wizard-step[data-step="twitter-tweet"] form textarea[name="message"]
  height: 180px
  color: #777777

#user-connector-wizard.modal .user-connector-wizard-step[data-step="twitter-tweet"] form button.btn-save
  position: relative
  padding: 10px 16px
  min-width: 140px

#user-connector-wizard.modal .user-connector-wizard-step[data-step="twitter-tweet"] form button.btn-save > i
  position: absolute
  top: 8px
  left: 15px
  font-size: 24px
