.workflow
  position: relative
  &.disabled
    > *
      opacity: 0.5
    > .add-work
      opacity: 1

.workflow:not(.workflow-from-lead)
  position: relative
  overflow-y: hidden
  margin: 0 -50px
  padding: 0 30px

.workflow.workflow-from-lead
  margin: 0 -30px

// .workflow.workflow-from-lead:before
  position: absolute
  top: 100%
  left: 0
  width: 100%
  height: 10px
  border-bottom: 1px solid #eeeeee
  content: " "

// .workflow.workflow-from-lead.workflow-has-actions:before
