.workflow .workflow-inner
  padding: 0 20px

.workflow.workflow-from-lead .workflow-inner
  padding: 0 60px 0 90px

.workflow .flow-add-button
  display: block
  margin: auto
  padding-top: 12px
  padding-bottom: 10px
  outline: none
  border: none
  border-radius: 4px
  background: none
  background-color: orange
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.15)
  color: #ffffff
  text-transform: uppercase
  font-weight: 500
  font-size: 16px
  transition: all 0.4s

.workflow.workflow-from-lead .flow-add-button
  margin-top: 25px

.workflow .flow-beginning-mark
  margin-bottom: 30px
  text-align: center

// .workflow:not(.workflow-from-lead) .flow-beginning-mark
