.workflow .flow-beginning-mark h3
  color: #cccccc
  text-transform: uppercase
  font-weight: 600
  font-size: 20px
  font-family: inherit

.workflow .flow-beginning-mark img
  width: 60px
  opacity: 0.05

.flow-header
  position: relative
  margin-bottom: 30px
  padding: 10px
  padding-bottom: 30px
  border: 1px solid #cccccc
  border-radius: 4px
  background-color: #ececec
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.15)
  color: #666666
  text-align: center
  font-weight: 600
  font-size: 120%

.workflow.workflow-from-lead .flow-header
  margin-top: 10px

// .workflow:not(.workflow-from-lead) .flow-header
  width: 249px

.flow-header h3
  display: inline-block
  margin: 0
  padding: 6px 10px
  padding-right: 20px
  border-radius: 4px
  color: inherit
  white-space: pre-wrap
  word-break: break-all
  font-weight: inherit
  font-size: inherit

.flow-header .flow-options-popup-toggle
  position: absolute
  bottom: 0
  left: 0
  padding: 0.25em 0.5em 0.25em 1em
  width: 100%
  border-top: 1px solid #cccccc
  border-radius: 0 0 4px 4px
  background: #ffffff
  color: #999999
  text-align: left
  text-transform: uppercase
  font-weight: normal
  font-size: 70%
  cursor: pointer
  transition: all 0.4s

.flow-header .flow-options-popup-toggle:hover
  color: #666666

.flow-header .switch
  float: right
  opacity: 0.75
  &:hover
