.workflow .work
  position: relative
  margin-bottom: 15px
  margin-bottom: 80px
  width: 100%
  border-radius: 4px
  background: #ffffff
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.15)
  cursor: pointer
  transition: box-shadow 0.4s

// .workflow:not(.workflow-from-lead) .work
  width: 249px

.workflow .work:hover
