  padding: 13px 0
  border-top-left-radius: 4px
  border-top-right-radius: 4px
  background-color: #0ab8ff
  color: #ffffff
  text-align: center
  font-size: 16px

.workflow .work[data-work-module-class="email"] .work-header
  background-color: #dc3e31

.workflow .work[data-work-class="email_video"] .work-header
  background-color: #79b260

.workflow .work[data-work-module-class="event_time"] .work-header
  background-color: #f6bf48

.workflow .work[data-work-module-class="physicalmail"] .work-header
