.workflow.workflow-from-lead .work.completed .status:before
  background: #34a2cc

.workflow.workflow-from-lead .work.completed .status:after
  background: #34a2cc

.workflow.workflow-from-lead .work.completed .status .point
  border-color: #34a2cc

.workflow.workflow-from-lead .work.completed .status .point:before
  background: #34a2cc

.workflow.workflow-from-lead .work.completed .status .point:after
  background: #34a2cc

.workflow.workflow-from-lead .work
