              display: block
              margin: 0 auto
              width: auto
              height: 50%
              border: 1px solid #cccccc
              border-radius: 6px
              box-shadow: 0 0 5px rgba(0, 0, 0, 0.25)
          .front-container
            position: relative
            overflow: hidden
            height: 100%
            border: 2px solid #cccccc
            border-radius: 6px
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.25)
            text-align: center
            .front-image-container
              position: relative
              overflow: hidden
              width: 100%
              height: 100%
              border-radius: 4px
              img.front-image
                width: 100%
                height: 100%
              .image-upload-container
                position: absolute
                top: 0
                left: 0
                padding: 50px
                width: 100%
                height: 100%
                transition: all 0.4s
                > *
                  display: block
                  margin: auto
                  margin-bottom: 15px
                img.image-upload
                  margin-top: 40px
                .btn-image-upload
                  display: inline-block
                  padding: 12px 20px
                  outline: none
                  border: none
                  border-radius: 24px
                  background-color: #0ab8ff
                  color: #ffffff
                  font-size: 24px
                  line-height: 24px
                  transition: all 0.4s
                .btn-image-upload:hover, .btn-image-upload:focus, .btn-image-upload:active
                  background-color: #00aaf0
                  text-decoration: none
                .note
                  color: #cccccc
                .template-list-toggle-wrapper
                  margin-top: 60px
                  color: #666666
                  font-size: 18px
                  transition: all 0.4s
                  a.template-list-toggle
                    color: #666666
                    text-decoration: underline
                    font-size: 18px
            .front-image-container.image-selected
              .image-upload-container
                visibility: hidden
                opacity: 0
            .front-image-container.image-selected:hover
              .image-upload-container
                visibility: visible
                opacity: 1
          .back-container
            position: relative
            padding-top: 160px
            padding-top: 12em
            height: 100%
            border: 2px solid #cccccc
            border-radius: 6px
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.25)
            text-align: center
            transition: padding-top 0.5s
            // overflow: hidden
            > a.back-image-toggle
              position: absolute
              top: 12em
              left: 50%
              z-index: 1
              margin-top: -8px
              margin-left: -8px
              width: 16px
              height: 16px
              border-radius: 50%
              background: #999999
              color: #999999
              color: #ffffff
              text-decoration: none
              line-height: 14px
              transition: all 0.4s
              i
                font-size: 10px
              i.fa:before
                content: "\f077"
            .back-image-container-wrapper
              position: absolute
              top: 0
              left: 0
              width: 100%
              height: 12em
              transition: all 0.4s
              .back-image-container
                position: relative
                overflow: hidden
                width: 100%
                height: 100%
                border-radius: 4px 4px 0 0
                background: #efefef
                > img.back-image
                  width: 100%
                  height: 100%
                .image-upload-container
                  position: absolute
                  top: 0
                  padding: 20px
                  width: 100%
                  height: 100%
                  transition: all 0.4s
                  > *
                    display: block
                    margin: auto
                    margin-bottom: 5px
                  img.image-upload
                    margin-top: 5px
                  a.btn-image-upload
                    display: inline-block
                    color: #999999
                    text-decoration: underline
                  .template-list-toggle-wrapper
                    color: #999999
                    transition: all 0.4s
                    a.template-list-toggle
                      color: #999999
                      text-decoration: underline
              .back-image-container.image-selected
                .image-upload-container
                  visibility: hidden
                  opacity: 0
              .back-image-container.image-selected:hover
                .image-upload-container
                  visibility: visible
                  opacity: 1
            .card
              padding: 20px
              padding: 0.5625em
              height: 100%
              .card-message-container-wrapper
                padding-right: 260px
                // 30.375em
                padding-right: 54em * 3.5in / 6in - 1.125em
                height: 100%
                .card-message-container
                  position: relative
                  height: 100%
                  .card-message
                    position: relative
                    overflow: hidden
                    padding: 0.5em
                    height: 100%
                    border: 1px solid #cccccc
                    border-radius: 4px
                    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset
                    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s
                  textarea
                    display: block
                    overflow: none
                    padding: 0
                    width: 100%
                    outline: none
                    border: none
                    border: none
                    -webkit-box-shadow: none
                    -moz-box-shadow: none
                    box-shadow: none
                    box-shadow: none
                    resize: none
                    transition: none
                  .token-dropdown
                    position: absolute
                    top: 5px
                    right: 5px
              .card-addresses-container
                position: relative
                float: right
                width: 240px
                // 29.375em
                width: 54em * 3.5in / 6in - 1.125em - 1em
                height: 100%
                text-align: left
                // .from-address-toggle
                .from-address-wrapper
                  display: none
                  .row
                    margin-right: -2.5px
                    margin-bottom: 5px
                    margin-left: -2.5px
                    .col-md-12, .col-md-6, .col-md-3
                      padding-right: 2.5px
                      padding-left: 2.5px
                  label.from-address-toggle
                    position: relative
                    margin-top: 2px
                    margin-bottom: 0
                    padding-left: 20px
                    font-weight: normal
                    font-size: 11px
                  label.from-address-toggle:before
                    position: absolute
                    top: 0
                    left: 0
                    display: inline-block
                    width: 16px
                    height: 16px
                    border: 1px solid #333333
                    content: " "
                .image-postcard-wrapper
                  margin-top: 15px
                  text-align: right
                .to-address-toggle
                  position: absolute
                  bottom: 0
                  .name, .company-name, .addressline1-addressline2, .ciy-state-zip-country
                    color: #666666
                    font-size: 20px
                    line-height: 20px
                  .note
                    margin-top: 20px
                    color: #999999
                    font-size: 11px
                .to-address-wrapper
                  position: absolute
                  bottom: 0
                  display: none
                  .row
                    margin-right: -2.5px
                    margin-bottom: 5px
                    margin-left: -2.5px
                    .col-md-12, .col-md-6, .col-md-3
                      padding-right: 2.5px
                      padding-left: 2.5px
                  .row:last-child
                    margin-bottom: 0
              .card-addresses-container.from-address-visible
                > .from-address-toggle
                  display: none
                .from-address-wrapper
                  display: block
              .card-addresses-container.to-address-visible
                > .to-address-toggle
                  display: none
                .to-address-wrapper
                  display: block
          .back-container.back-image-hide
            padding-top: 0
            > a.back-image-toggle
              top: 0
              i.fa:before
                content: "\f078"
            .back-image-container-wrapper
              visibility: hidden
              height: 0
              opacity: 0
  .tab-pane.left-pane-open
    > .left-pane-toggle
      visibility: hidden
      opacity: 0
    .left-pane-wrapper
      transform: translate3d(0, 0, 0)
    .main-pane-wrapper
      padding-right: 0
      padding-left: 240px
      .front-container
        .template-list-toggle-wrapper
          visibility: hidden
          opacity: 0
      .back-container
        .template-list-toggle-wrapper
          visibility: hidden
          opacity: 0

