#user-info-popup.modal .modal-dialog
  width: 640px

#user-info-popup.modal .modal-dialog .modal-header
  padding-top: 30px
  border-bottom: none
  background-color: initial
  color: initial
  text-align: center

#user-info-popup.modal .modal-dialog .modal-header h4
  color: #333333
  font-size: 24px

#user-info-popup.modal .modal-dialog .modal-footer
  padding-bottom: 30px
  border-top: none
  text-align: center

#user-info-popup.modal .modal-dialog .modal-footer button.btn-save
  min-width: 100px

#user-info-popup.modal .modal-dialog .modal-body form.user-info-form
  padding: 0 50px
  text-align: left

#user-info-popup.modal .modal-dialog .modal-body form.user-info-form .form-group
  margin-bottom: 15px

#user-info-popup.modal .modal-dialog .modal-body form.user-info-form .form-group label
  margin-bottom: 0

#user-info-popup.modal .modal-dialog .modal-footer .note
  margin: 0 80px
  margin-bottom: 15px
  color: #cccccc
  font-size: 90%
