.task-list
  position: relative
  margin-top: 30px
  color: #999999

.task-list .task-content
  position: relative
  padding: 15px
  border: 1px solid #cecece
  border-radius: 4px
  background: #ffffff

.task-list .task-content:before, .task-list .task-content:after
  position: absolute
  top: 33px
  right: 100%
  width: 0
  height: 0
  border: solid transparent
  content: " "
  pointer-events: none

.task-list .task-content:after
  margin-top: -10px
  border-width: 10px
  border-color: transparent
  border-right-color: #ffffff

.task-list .task-content:before
  margin-top: -12px
  border-width: 12px
  border-color: transparent
  border-right-color: #cecece

.task-list .task [data-name]
  color: #343c3f
  transition: 0.5s color

.task-list .task.task-completed [data-name]
  color: #9ca6b3

.task-list .task .status-date-wrapper
  float: right
  color: #34a2cc
  font-size: 12px

.task-list .task [data-name="due_at"]
  color: #34a2cc

.task-list .task.task-completed [data-name="due_at"]
  color: #9ca6b3

.task-list .task .task-status.task-status-completed
  display: none

.task-list .task.task-completed .task-status.task-status-completed
  display: inline
  color: #9ca6b3

.task-list .task.task-completed .task-status.task-status-due
  display: none

.task-list .task h3[data-name="type"]
  margin-top: 0
  font-weight: 600
  font-size: 16px

.task-list .task p[data-name="description"]
  overflow-x: auto
  padding: 10px 0
  white-space: pre-wrap

.task-list .task p[data-name="description"] + .editable-container, .task-list .task p[data-name="description"] + .editable-container .form-inline .form-group, .task-list .task p[data-name="description"] + .editable-container .form-inline .form-group .editable-input
  display: block

.task-list .task p[data-name="description"] + .editable-container .form-inline .form-group .editable-input .form-control
  width: 100%

.task-list .task .mark
  position: absolute
  top: 20px
  left: -40px
  display: inline-block
  padding: 0
  width: 27px
  height: 100%
  background-color: transparent
  text-align: center

.task-list .task:not(:last-child) .mark:after
  position: absolute
  top: 28px
  left: 13px
  width: 1px
  height: 100%
  background: #cecece
  content: " "

.task-list .task .mark .icon
  display: inline-block
  width: 27px
  height: 27px
  border: 1px solid #cecece
  border-radius: 50%
  background: #ffffff
  color: #34a2cc
  line-height: 29px

.task-list .task.task-completed .mark .icon
  color: #cecece
